.App {
  background-color: black;
  flex: 1;
  display: flex;
  height: 100vh;
  width: 100%;
  /* top: 0;
  left: 0;
  right: 0;
  bottom: 0; */
}

.App > p {
  color: aliceblue;
  margin: 0;
  position: absolute;
  text-align: center;
  top: 60%;
  font-size: 20px;
  left: 50%;

  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

@media (max-width: 600px) {
  .App > p {
    top: 52%;
    font-size: 14px;
  }
}

.App > a {
  color: aliceblue;
  margin: 0;
  position: absolute;
  text-align: center;
  top: 70%;
  font-size: 20px;
  text-decoration: solid;
  border-style: solid;
  padding: 10px;
  left: 50%;

  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

@media (max-height: 600px) {
  .App > a {
    top: 66%;
    font-size: 15px;
  }
}

.App > a:hover {
  color: black;
  background-color: white;
}


.App-logo {
  margin: 0;
  position: absolute;
  top: 30%;
  left: 50%;
  width: 250px;
  height: 250px;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

@media (max-height: 600px) {
  .App-logo {
    top: 25%;
  }
}

.footer {
  bottom: 0%;
  position: absolute;

  width: 100%;

  box-shadow: 0px 0px 0px 1px#fff;
  text-align: center;
  align-content: space-around;
  background-color: black;
  flex-direction: row;
  padding-top: 8px;
  padding-left: 8px;
  padding-right: 8px;
}

.footer > .bt1 {
  margin-right: 20px;
  margin-bottom: 10px;
}
